	module.exports = {"data":{"markdownRemark":{"htmlAst":{"type":"root","children":[{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Here is a custom design of a jean jacket that I had to draft and sew for a school project. With the weather becoming warmer, I wanted to create a light pastel jean jacket that looks different from other typical jean jackets that one can find on the market. As I had suspected, there were only black and dark indigo denims at the fabric store; but I was not ready to give up yet. I went to Maiwa at Granville Island to buy a cyan dye and dyed my white twill fabric in my bathtub. "}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"To keep the design minimal, I decided to not use contrasting thread that is commonly seen on other denim jackets. I was happy with the result! "}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Features:"},{"type":"element","tagName":"br","properties":{},"children":[]},{"type":"text","value":"\n•\tPatch pockets"},{"type":"element","tagName":"br","properties":{},"children":[]},{"type":"text","value":"\n•\tBack Pleat"},{"type":"element","tagName":"br","properties":{},"children":[]},{"type":"text","value":"\n•\tOversized fit"},{"type":"element","tagName":"br","properties":{},"children":[]},{"type":"text","value":"\n•\tWaist belt "}]}],"data":{"quirksMode":false}},"frontmatter":{"path":"/garments/personal/cyan-denim-jacket","title":"Cyan Denim Jacket","origin":"/garments/personal"}},"allFile":{"edges":[{"node":{"name":"DenimJacket","relativePath":"denimjacket/DenimJacket.jpeg"}},{"node":{"name":"DenimJacket2","relativePath":"denimjacket/DenimJacket2.jpeg"}},{"node":{"name":"DenimJacket3","relativePath":"denimjacket/DenimJacket3.jpeg"}}]}},"pathContext":{"imageFolder":"denimjacket"}}

